<template>
  <div class="posts-item">
    <div class="img">
      <img :src="posts.headimgurl" alt="">
    </div>
    <div class="infos">
      <router-link class="title" :to="'/forum/posts/' + posts.forum_id">
        <h3>{{posts.title}}</h3>
      </router-link>
      <p class="info">{{posts.user_name}}</p>

      <a
        href="javascript:void(0)"
        class="uncollect"
        @touchstart="uncPosts"
      >取消收藏</a>
      <mt-badge size="small" type="success" style="position: absolute; right: 0; top: 50%;">{{posts.reply}}</mt-badge>
    </div>
  </div>
</template>

<script>
import bus from '../../../assets/js/bus'
export default {
  name: 'Collect-Posts-Item',
  props: {
    posts: Object
  },
  methods: {
    uncPosts () {
      bus.$emit('unCollectPosts', this.posts)
    }
  }
}
</script>

<style lang="less" scoped>
  .posts-item{
    display: flex;
    padding: .5rem .4rem;
    background-color: #fff;
    margin-top: .6rem;
    .img{
      width: 4.6rem;
      height:4.6rem;
      img{
        border-radius: 50%;
        width: 100%;
      }
    }
    .infos{
      flex: 1;
      padding-left: .6rem;
      position: relative;
      overflow: hidden;
      .title{
        font-size: 14px;
        font-weight: 500;
        color: #333;
        h3 {
          font-size: 14px;
          margin: 0;
        }
      }
      p{
        margin: 0;
      }
      .info{
        font-size: 12px;
        color: #999;
      }
      .uncollect{
        font-size: 12px;
        color: cornflowerblue;
      }
    }
  }
</style>



