<template>
  <div class="goods-item">
    <router-link :to="'/mall/item/' + goods.mall_id" class="img">
      <img src="/static/img/hongyanbg.jpg" alt="">
    </router-link>
    <div class="info">
      <router-link :to="'/mall/item/' + goods.mall_id">
        <h3>{{goods.title}}</h3>
      </router-link>
      <span class="price">¥{{goods.price_min}}</span>
      <a href="javascript:void(0)" class="uncollect" @touchstart="uncollect">取消收藏</a>
      <i class="fa fa-angle-right detail" aria-hidden="true"></i>
    </div>
  </div>
</template>

<script>
import bus from '../../../assets/js/bus'
export default {
  name: 'CollectGoodsItem',
  props: {
    goods: Object
  },
  methods: {
    uncollect () {
      bus.$emit('unCollectGoods', this.goods)
    }
  }
}
</script>

<style lang="less" scoped>
  .goods-item{
    display: flex;
    padding: .5rem .4rem;
    background-color: #fff;
    margin-top: .6rem;
    .img{
      width: 4.6rem;
      height:4.6rem;
      img{
        width: 100%;
      }
    }
    .info{
      flex: 1;
      padding-left: .6rem;
      position: relative;
      a{
        color: #333;
      }
      h3{
        margin: 0;
        padding-top: 1rem;
        font-size: 14px;
      }
      .price{
        font-size: 12px;
        color: brown;
      }
      .uncollect{
        font-size: 12px;
        color: cornflowerblue;
      }
      .detail{
        position: absolute;
        font-size: 1rem;
        color: #999;
        right: 0;
      }
    }
  }
</style>



