<template>
  <div class="ui-wrap">
    <AppHeader title="我的收藏" showBack="true"></AppHeader>
    <div class="collect-wrap">
      <mt-navbar v-model="selected">
        <mt-tab-item id="goods">商品</mt-tab-item>
        <mt-tab-item id="posts">帖子</mt-tab-item>
      </mt-navbar>
      <!-- tab-container -->
      <mt-tab-container v-model="selected">
        <mt-tab-container-item id="goods">
          <GoodsItem
            v-for="(item, index) in goodsList"
            :key="index"
            :goods="item"
          ></GoodsItem>
        </mt-tab-container-item>
        <mt-tab-container-item id="posts">
          <PostsItem
            v-for="(item, index) in postsList"
            :key="index"
            :posts="item"
          ></PostsItem>
        </mt-tab-container-item>
      </mt-tab-container>
    </div>
  </div>
</template>

<script>
import AppHeader from '../../../components/header/header.vue'
import GoodsItem from './collect-goods-item.vue'
import PostsItem from './collect-posts-item.vue'
import bus from '../../../assets/js/bus'
import {getCollectList, deleteCollect} from '../../../api/home'
import { Toast } from 'mint-ui'
export default {
  name: 'Collect',
  data () {
    return {
      selected: 'goods',
      goodsList: [],
      postsList: []
    }
  },
  created () {
    this.getDatas()
  },
  beforeDestory () {
    bus.$off('unCollectPosts')
    bus.$off('unCollectGoods')
  },
  mounted () {
    bus.$on('unCollectPosts', posts => {
      deleteCollect(posts.collect_id)
        .then(data => {
          if (data.state) {
            Toast('删除成功')
            this.getDatas()
          } else {
            Toast(data.errormsg || '删除失败')
          }
        })
    })
    bus.$on('unCollectGoods', goods => {
      deleteCollect(goods.collect_id)
        .then(data => {
          if (data.state) {
            Toast('删除成功')
            this.getDatas()
          } else {
            Toast(data.errormsg || '删除失败')
          }
        })
    })
  },
  methods: {
    getDatas () {
      getCollectList(1)
        .then(data => {
          if (data.state) {
            this.$set(this, 'goodsList', data.data.list)
          } else {
            Toast(data.errormsg)
          }
        }).catch(err => {
          Toast('获取数据失败')
          console.log(err)
        })
      getCollectList(2)
        .then(data => {
          if (data.state) {
            this.$set(this, 'postsList', data.data.list)
          } else {
            Toast(data.errormsg)
          }
        }).catch(err => {
          Toast('获取数据失败')
          console.log(err)
        })
    }
  },
  components: {
    AppHeader,
    GoodsItem,
    PostsItem
  }
}
</script>

<style lang="less" scoped>
  .collect-wrap{
    /*background-color: #fff;*/
  }
</style>



